.page {
  background-color: #EFEFEF;
}
.tabbar-wrap {
  height: 3.125rem;
  position: fixed;
  bottom: 0;
  /* 距离底部为0像素 */
  left: 0;
  /* 左侧与视口边界对齐 */
  width: 100%;
  /* 宽度等于视口宽度 */
  z-index: 9999;
}
.home-page {
  width: 100%;
  height: 100%;
  overflow: auto;
  background: transparent;
  color: #444;
}
.product-list-wrap {
  width: 100%;
  display: flex;
  margin: 0.625rem 0;
  box-sizing: border-box;
}
.product-list-wrap .categories-list {
  width: 13.125rem;
  flex: 0 0 13.125rem;
  background-color: #fff;
}
.product-list-wrap .product-list {
  flex: auto;
}
